import { datasync } from '../utils/datasync.yandex';
import { sortBy } from '@/utils/common.js';

export default {
  namespaced: false,
  state: {
    summary: 0,  // only positive score per day
    day: new Date().setHours(0, 0, 0, 0),
    fullSummary: 0, // :off  on:undefined,
    datasync: null,
  },
  getters: {
    summary: (state) => state.summary,
    summaryGoals: (state, getters) => Math.floor(Math.round(state.summary) / getters.settings.score.limit.day),
    // summary by items – only current dictionary
    fullSummary(state, getters) {
      if (state.fullSummary === undefined)
        state.fullSummary = Math.round(
          getters.items.reduce((sum, item) => sum + item.score.value, 0)
        );
      return state.fullSummary;
    },
    unfoldScore: () => ({ dispatch, getters }, item) => {
        const ratio = getters.settings.score.ratio;
        const data = item?.score?.data || {};
        // text length factor
        const lengthFactor =
          item.text.length > 30 ? 1.5 : item.text.length < 5 ? 0.9 : 1;

        item.score = {
          data,
          toString() {
            return this.formatted;
          },
          valueOf() {
            return this.value;
          },
          get value() {
            return Object.keys(this.data).reduce(
              (sum, name) => sum + this.data[name] * ratio[name],
              0
            );
          },
          get formatted() {
            return Number(this.value).toFixed(2);
          },
          get info() {
            return Object.keys(ratio).map((name) => {
              return {
                name,
                value: Number((this.data[name] || 0) * ratio[name]).toFixed(2),
              };
            });
          },
          clear() {
            Object.keys(this.data).forEach((name) => {
              this.data[name] = 0;
            });
            dispatch('syncDictionary', item);
            // const val = this.value;
            // dispatch('syncSummary', -val);
          },
          exam({ name, result, score = 1 }) {
            /* result: {'success' || 'skip' || 'error'} */
            const index =
              // length factor
              lengthFactor *
              // complexity factor
              (getters.settings.training.complexity[name] || 1) *
              // listening factor
              (getters.settings.training.mode.listening &&
                ['crossword', 'docking', 'writing'].includes(name)
                ? 0.9
                : 1.5);

            switch (result) {
              case 'spoiled': // success by peek
                score *= 0.5;
              // eslint-disable-next-line no-fallthrough
              case 'success':
                score *= index;
                break;
                // for trying
              case 'error':
                score *= 0.8;
              // eslint-disable-next-line no-fallthrough
              case 'skip':
                score /= -index;
            }

            dispatch('syncSummary', score * ratio[name]);

            let sum = (this.data[name] || 0) + score;
            sum = sum > 0 ? sum : 0;
            // console.log('exam', name, result, item.text, score, this.data[name], sum, this.data, item.id);
            if (this.data[name] === sum) return;

            if ((this.data[name] || 0) == sum) return;
            this.data[name] = sum;
            dispatch('syncDictionary', item);
          },
          set formatted(val) {
            // trash && sodomy
            // check: info: Uncaught (in promise) TypeError: Cannot set property value of #<Object> which has only a getter at eval (bootstrap-vue-next.es.js?9bd3:185:1)
          },
          set value(val) {
            // trash && sodomy
            // check: info: Uncaught (in promise) TypeError: Cannot set property value of #<Object> which has only a getter at eval (bootstrap-vue-next.es.js?9bd3:185:1)
          },
          set info(val) {
            // trash && sodomy
            // check: info: Uncaught (in promise) TypeError: Cannot set property value of #<Object> which has only a getter at eval (bootstrap-vue-next.es.js?9bd3:185:1)
          },
        };
        return item.score;
      },
  },
  actions: {
    async fetchSummary({ state, dispatch }) {
      const data = [
        JSON.parse(window.localStorage.getItem('day-summary')) || {},
        await dispatch('fetchSummaryDataSync'),
      ];
      const { day, summary } = sortBy(data, 'updated', true)[0];
      if (day !== new Date().setHours(0, 0, 0, 0)) return;
      state.summary = summary;
    },
    async fetchSummaryDataSync({ state }) {
      state.datasync = datasync('storage');
      return state.datasync
        .get('daySummary')
        .then((items) => {
          return items.get('daySummary')?.item;
        })
        .catch((e) => console.error(e));
    },
    async syncSummary({ state }, score = 0) {
      if (score < 0) return;
      score = state.summary + score;
      state.summary = score;
      const updated = Date.now();

      const summary = {
        summary: score,
        day: state.day,
        updated,
      };

      window.localStorage.setItem('day-summary', JSON.stringify(summary));
      state.datasync
        .set('daySummary', { items: summary, updated })
        .catch((e) => console.error(e));
    },
  },
};
