// Условия использования и ограничения
// https://yandex.ru/dev/datasync/http/doc/legal/index.html

import axios from 'axios';
import merge from 'deepmerge';
import { sortBy } from '@/utils/common.js';

const {
  token = window.localStorage.getItem('auth-yandex-token'),
  context = 'app',
  baseURL = `https://cloud-api.yandex.net/v1/data/${context}/databases/`,
  headers = {
    Authorization: `OAuth ${token}`,
  },
} = {};

const parseName = (id) => {
  const matches = (/^((settings)|(collections)|((dictionary)-(.+)))$/.exec(id))||[];
  const collectionID = matches[matches.length - 1];
  const name = matches[collectionID ? matches.length - 2 : 0];
  return { name, collectionID };
};

const inited = {};
const database = (name) => {
  const base_revision = (name) => {
    const id = `datasync-yandex-${name}-revision`;
    let _value = window.localStorage.getItem(id) || 0;
    return {
      toString() {
        return _value;
      },
      valueOf() {
        return _value;
      },
      get() {
        return _value;
      },
      set(revision) {
        window.localStorage.setItem(id, revision);
        _value = revision;
      },
    };
  };

  const data = {
    // settings: () => {
    //   const id = 'settings';
    //   inited[id] ||= {
    //     id,
    //     revision: base_revision(id),
    //     collection: {
    //       id: 'records',
    //       fields: {
    //         id: { field_id: 'id', value: { type: 'string' } },
    //         data: { field_id: 'data', value: { type: 'string' } },
    //       },
    //     },
    //     info: null,
    //   };
    //   return inited[id];
    // },
    storage: () => {
      const id = 'storage';
      inited[id] ||= {
        id,
        revision: base_revision(id),
        collection: {
          id: 'daySummary',
          fields: {
            day: { field_id: 'day', value: { type: 'integer' } },
            summary: { field_id: 'summary', value: { type: 'double' } },
          },
          getChanges(item) {
            return {
              change_type: 'set',
              collection_id: 'daySummary',
              record_id: 'daySummary',
              changes: [
                {
                  change_type: 'set',
                  field_id: 'item',
                  value: {
                    type: 'string',
                    string: JSON.stringify(item),
                  },
                },
              ],
            };
          },
        },
        info: null,
      };
      return inited[id];
    },
    dictionary: (collectionID) => {
      const id = `dictionary-${collectionID}`;
      inited[id] ||= {
        id,
        revision: base_revision(id),
        collection: {
          id: collectionID,
          fields: {
            id: { field_id: 'id', value: { type: 'string' } },
            score: { field_id: 'score', value: { type: 'string' } }, // score.data
            data: { field_id: 'data', value: { type: 'string' } }, // text, translation, transcription, lang, native
          },
          getChanges(item) {
            return {
              change_type: item.text ? 'set' : 'delete',
              collection_id: item.collectionID || collectionID,
              record_id: item.id || collectionID,
              changes: [
                {
                  change_type: 'set',
                  field_id: 'item',
                  value: {
                    type: 'string',
                    string: JSON.stringify(item),
                  },
                },
              ],
            };
          },
        },
        info: null,
      };
      return inited[id];
    },
  };

  return data[name];
};

export const datasync = (name) => {
  let db;
  const getInfo = () => {
    if (db.info) return Promise.resolve(db.info);

    // get list databases
    return axios({
      method: 'get',
      url: `/`,
      baseURL,
      headers,
    })
      .then((response) => {
        response.data.items.forEach((item) => {
          const { name, collectionID } = parseName(item.database_id);
          if (!name) return; // todo: delete?
          const db = database(name)(collectionID);
          db.info = item;
        });

        return (
          db.info ||
          // create db if need
          axios({
            method: 'put',
            url: `/${db.id}/`,
            baseURL,
            headers,
          }).then((response) => {
            db.info = response.data;
            return db.info;
          })
        );
      })
      .catch((e) => {
        console.error(e);
      });
  };

  const state = {
    get: { readyState: 0, request: null },
    set: { readyState: 0, request: null },
  };
  return {
    get: (collectionID, force) => {
      if (!token) return Promise.reject(Error('OAuth is required'));

      db = database(name)(collectionID);
      if (!db) return Error(`DB[name="${name}"] is undefined`);
      if (!force && state.get.readyState) return state.get.request;

      state.get.readyState = 1;
      state.get.request = getInfo()
        .then((info) => {
          state.get.readyState = 2;
          if (!info) return Promise.reject(Error('Not info.revision'));
          if (!info.records_count) return Promise.resolve({}); // ?: records_count || revision === 0

          return axios({
            method: 'get',
            // url: `/${db.id}/deltas/?base_revision=0`,
            url: `/${db.id}/deltas/?base_revision=${db.revision}`,
            baseURL,
            headers,
          }).then((response) => {
            state.get.readyState = 3;
            const items = {};
            sortBy(response.data.items, 'revision').forEach((item) => {
              item.changes.forEach(({ record_id: id, change_type, changes }) => {
                items[id] ||= {};
                  if (change_type === 'delete') return (items[id] = {});
                  else {
                    changes.forEach(({ field_id, value }) => {
                      items[id][field_id] ||= [];
                      items[id][field_id].push(JSON.parse(value.string));
                    });
                  }
                });
            });

            // merge changes
            const itemsMap = new Map();
            Object.keys(items).forEach((id) => {
              const item = { id };
              Object.keys(items[id]).forEach((field_id) => {
                item[field_id] = merge.all(items[id][field_id]);
              });
              itemsMap.set(id, item);
            });

            db.revision.set(response.data.revision);
            return itemsMap;
          });
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          state.get.readyState = 0;
        });

      return state.get.request;
    },
    set: async (collectionID, { items, updated }, force) => {
      if (!token) return Promise.reject(Error('OAuth is required'));

      db = database(name)(collectionID);
      if (!db) return Error(`DB[name="${name}"] is undefined`);
      if (!force && state.set.readyState) return state.set.request;

      const data = {
        delta_id: `${updated}: [${navigator.userAgent}]`,
        changes: [],
      };
      [items].flat().forEach((item) => {
        data.changes.push(db.collection.getChanges(item));
      });

      state.set.readyState = 1;
      state.set.request = getInfo()
        .then((info) => {
          state.set.readyState = 2;
          if (!info) return Promise.reject(Error('Not info.revision'));

          return axios({
            method: 'post',
            url: `/${db.id}/deltas`,
            baseURL,
            data,
            headers: {
              ...headers,
              'If-Match': db.revision,
            },
          }).then((response) => {
            state.set.readyState = 3;
            db.revision.set(response.headers.etag);
            return response;
          });
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          state.set.readyState = 0;
        });

      return state.set.request;
    },
  };
};
